.App {
  height: auto;
  width: 100vw;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 0%;
  margin: 0%;
  font-family: 'Fredoka', sans-serif;
  overflow-x: hidden;
}

.title{
  background-color: #35c055;
  text-align: center;
  margin: 0%;
  color: #203649;
  text-transform: uppercase;
  padding-top: 20px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  
}

.footer{
  background-color: #35c055;
  font-family: 'Fredoka', sans-serif;
  font-weight: 900;
  color: #203649;
  padding: 15px;
  text-align: center;
  font-family: 25px;
}

h1{
  color:#35c055;
  font-family: 'Fredoka', sans-serif;
  font-weight: 900;
  padding: 10px;
  background-color: #203649;
  margin: 0%
}

h3{
  color: black;
  font-family: 'Fredoka', sans-serif;
  font-weight: 900;
}

.cryptoHeader {
  width: 100%;
  height: 100px;
  color: #203649;
  background-color:#35c055;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cryptoHeader input {
  width: 80%;
  height: 50px;
  border: none;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  font-size: 20px;
  font-weight: bold;
  color: rgb(0, 0, 0);
  padding-left: 15px;
  font-family: 'Fredoka', sans-serif;
}

.cryptoDisplay {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.coin {
  width: 80%;
  height: 300px;
  border: 2px solid #203649;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 5px 10px;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
}

.coin img {
  height: 25%;
}